export default{
	data(){
		return{
			headerTop: 0,
			headHeight:0,
			current: 0,
			headSwiper:[],//顶部轮播图数组
			headNav:[],//顶部导航
			goods:[],//商品
			recommend:[],//商品推荐
			imgHttp:'', //图片接口前缀
			moreType:0, //更多类型
			searchKey:'',//搜索关键字
			moreType1:'',
			moreType2:'',
			
		}
	},
	
	methods:{
		//跳转到信息
		to_message(){
			uni.navigateTo({
				url:'../my/message_list'
			})
		},
		//搜索跳转
		toSearch(){
			uni.navigateTo({
				url:'../commodity/search?key=' + this.searchKey
			})
		},
		//轮播图跳转
		toSwiper(type,id){
			if(type == 1){
				uni.navigateTo({
					url:"../commodity/goods_details?id=" + id
				})
			} else if(type == 2){
				uni.navigateTo({
				    url:"./web_view?id=" + id
				})
			} else if(type == 3){
				uni.navigateTo({
					url:"../find/find_details?id=" + id
				})
			} else if(type == 4){
				uni.navigateTo({
				    url:"./web_view?id=" + id
				})
			}
		},
		//跳转到分类
		toCommmodity(type){
			console.log(type)
			uni.setStorageSync('actNav',type);
			uni.switchTab({
				url:'../commodity/commodity' 
			})
		},
	    //跳转到商品详情
		toDetails:function(id){
			uni.navigateTo({
				url:'../commodity/goods_details?id=' + id
			})
		},
		//切换打折专区
		currentDiscount:function(num){
			this.current = num
		},
		//获取顶部轮播图
		getSwiperTop:function(){
			let self = this;
			this.ask("/app/index/getIndexCarousel","GET",{pid:'index'},function(res){
				self.headSwiper = res.data.data;
				
			})
		},
		//获取头部导航
		getHeadNav:function(){
			let self = this;
			this.ask("/app/index/getIndexClassify","GET",{},function(res){
				self.headNav = res.data.data
				console.log(self.headNav)
			})
		},
		//获取推荐商品
		getRecommend:function(){
			let self = this;
			this.ask("/app/index/getIndexHot","GET",{},function(res){
				console.log(res)
				self.recommend = res.data.data
				// console.log(self.recommend)
			})
		},
		//获取顶部商品列表
		getGoods:function(){
			let self = this;
			this.ask("/app/index/getProductListForIndex","GET",{},function(res){
			   
				for(let i = 0 ; i<res.data.data.length;i++){
					if(res.data.data[i].id == '1'){
						self.moreType1 = res.data.data[i].id;
						self.goods = res.data.data[i].productlist;
						self.moreType = i;
					}
				}
				console.log(res)
			})
		}
		
	},
	onLoad:function(){
		this.getSwiperTop();
		this.getHeadNav();
		this.getGoods();
		this.getRecommend();
		this.imgHttp = this.comHttp;
	},
	created() {
		this.headerTop = this.StatusBar + 'px';
		this.headHeight = this.CustomBar + 'px';
	}
}
